<template>
  <div>
    <div id="show_box">
      <div class="item">
        <div class="title">
          统一客户名称*
        </div>
        <input v-model="unify_name" class="content_input"/>
      </div>
      <div class="item">
        <div class="title">
          客户名称
        </div>
        <input v-model="name" class="content_input"/>
      </div>
      <div class="item half">
        <div class="title">
          等级
        </div>
        <v-select class="content_input no_border" v-model="level" :options="['一级','二级','三级','四级','五级','六级']" :searchable="false"></v-select>
      </div>
      <div class="item half">
        <div class="title">
          标记
        </div>
        <input v-model="mark" class="content_input"/>
      </div>
      <div class="item half">
        <div class="title">
          网络经理
        </div>
        <input v-model="N_manager" class="content_input"/>
      </div>
      <div class="item half">
        <div class="title">
          网络经理电话
        </div>
        <input v-model="NM_phone" class="content_input"/>
      </div>
      <div class="item half">
        <div class="title">
          客户经理
        </div>
        <input v-model="C_manager" class="content_input"/>
      </div>
      <div class="item half">
        <div class="title">
          客户经理电话
        </div>
        <input v-model="CM_phone" class="content_input"/>
      </div>
      <div class="item">
        <div class="title">
          来源
        </div>
        <input v-model="origin" class="content_input"/>
      </div>
      <div class="item">
        <div class="title">
          备注
        </div>
        <input v-model="remark" class="content_input"/>
      </div>
      <div class="item half">
        <div id="btn_confirm" class="btn" @click="cancel">
          取消
        </div>
      </div>
      <div class="item half">
        <div id="btn_confirm" class="btn primary" @click="confirm">
          确认
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Add',
  props: {
  },
  data:function(){
    return {
      unify_name:'',
      name:'',
      level:'',
      mark:'',
      N_manager:'',
      NM_phone:'',
      C_manager:'',
      CM_phone:'',
      origin:'',
      remark:'',
      post:null,
      search:null
    }
  },
  created:function(){
    this.fetchData();
  },
  watch:{
    '$route':'fetchData'
  },
  methods:{
    confirm:function(){
      let self = this;
      if(this.post != null && this.post.id != null){
        this.update_customer();
      }else{
        this.new_customer();
      }
    },
    new_customer:function(){
      let self = this;
      if(this.unify_name == undefined || this.unify_name == ''){
        alert("统一客户名称不能为空!")
        return;
      }
      let data = new FormData();
      data.append('unify_name',this.unify_name==undefined?'':this.unify_name);
      data.append("name",this.name==undefined?'':this.name);
      data.append("level",this.level==undefined?'':this.level);
      data.append("mark",this.mark==undefined?'':this.mark);
      data.append("N_manager",this.N_manager==undefined?'':this.N_manager);
      data.append("NM_phone",this.NM_phone==undefined?'':this.NM_phone);
      data.append("C_manager",this.C_manager==undefined?'':this.C_manager);
      data.append("CM_phone",this.CM_phone==undefined?'':this.CM_phone);
      data.append("origin",this.origin==undefined?'':this.origin);
      data.append("remark",this.remark==undefined?'':this.remark);
      this.axios
        .post('http://10.117.195.193/gensmo/scripts/add_customer.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            alert("添加成功")
            self.$router.push({
              name:'search',
              params:{
                search:self.search
              }
            })
          }
        })
        .catch(function(err){
          alert(err);
        })
    },
    update_customer:function(){
      let self = this;
      if(this.unify_name == ''){
        alert("统一客户名称不能为空!")
        return;
      }
      let data = new FormData();
      data.append('id',this.post.id);
      data.append('unify_name',this.unify_name==undefined?'':this.unify_name);
      data.append("name",this.name==undefined?'':this.name);
      data.append("level",this.level==undefined?'':this.level);
      data.append("mark",this.mark==undefined?'':this.mark);
      data.append("N_manager",this.N_manager==undefined?'':this.N_manager);
      data.append("NM_phone",this.NM_phone==undefined?'':this.NM_phone);
      data.append("C_manager",this.C_manager==undefined?'':this.C_manager);
      data.append("CM_phone",this.CM_phone==undefined?'':this.CM_phone);
      data.append("origin",this.origin==undefined?'':this.origin);
      data.append("remark",this.remark==undefined?'':this.remark);
      this.axios
        .post('http://10.117.195.193/gensmo/scripts/update_customer.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            alert("修改成功")
            self.$router.push({
              name:'search',
              params:{
                search:self.search
              }
            })
          }
        })
        .catch(function(err){
          alert(err);
        })
    },
    cancel:function(){
      let self = this;
      self.$router.push({
        name:'search',
        params:{
          search:self.search
        }
      })
    },
    fetchData:function(){
      if(this.$route.params.post != null){
        this.post = this.$route.params.post;
        this.unify_name = this.post.unify_name;
        this.name = this.post.name;
        this.level = this.post.level;
        this.N_manager = this.post.N_manager;
        this.NM_phone = this.post.NM_phone;
        this.C_manager = this.post.C_manager;
        this.CM_phone = this.post.CM_phone;
        this.origin = this.post.origin;
        this.remark = this.post.remark;
      }
      if(this.$route.params.search != null){
        this.search = this.$route.params.search;
      }
    }
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  background: #EAEAEA;
}
#show_box{
  height: fit-content;
  width:70vw;
  background: #FFFFFF;
  border-radius: 2vh;
  margin-left: 15vw;
  margin-right: 15vw;
  margin-bottom: 3vh;
  margin-top: 3vh;
  padding: 2vw;
}
.item{
 /* height: fit-content;*/
  width:67.4vw;
  margin-left: 1.3vw;
  margin-right: 1.3vw;
  margin-top: 12px;
}
.item.half{
  width:32.4vw;
  margin-left: 1.3vw;
  margin-right: 1.3vw;
  display: inline-block;
}
.item .title{
  height:14px;
  font-size:12px;
  color:#EA7777;
}
.item .content_input{
  height:fit-content;
  min-height: 30px;
  margin-top: 14px;
  width: -webkit-fill-available;
  font-size:20px;
  display:flex;
  border: 1px solid rgba(60,60,60,.26);
  border-radius: 4px; 
  align-items: center;
  word-break: break-all;
}
.item .content_input.no_border{
  border: none;
}
.item input.content_input{
  padding: 2px 0 4px 10px;
}
.item .btn{
  width:-webkit-fill-available;
  margin-top: 14px;
  border-radius:4px;
  height: 30px;
  font-size:15px;
  display:flex;
  justify-content:center;
  align-items:center;
  background:#CDCDCD;
  color:#000000;
  user-select:none;
}
.item .btn:hover{
  background:#BCBCBC;
}

.item .btn:active{
  background:#AAAAAA;
  color:#FFFFFF;
}
.item .btn.primary{
  background: #EA7777;
  color: #FFFFFF;
}
.item .btn.primary:hover{
  background:#D96666;
}
.item .btn.primary:active{
  background:#C85555;
  color:#000000;
}
</style>