import Vue from 'vue'
import Router from 'vue-router'
import Search from './components/Search.vue'
import Add from './components/Add.vue'

Vue.use(Router)

// Router的内置属性
export default new Router({
  routes: [              
    {
      path: '/Add',       
      name: 'add',
      component: Add
    },
    {
      path: '/',   
      name: 'search',
      component: Search
    }
  ]
})